import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

import * as $ from 'jquery';


import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { EventDefinitionBean } from '../bean/event-definition.bean';


import * as moment from 'moment';
import * as _ from 'lodash';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
/**
 * 触发规则
 */
@Component({
	selector: 'ecenter-triggerrule',
	templateUrl: './ecenter-triggerrule.component.html',
	styleUrls: ['./ecenter-triggerrule.component.scss'],
	providers: [CusOperationHttpService]
})
export class EcenterTriggerruleComponent implements OnInit {

	ch: any = CALENDAR_CN;
	tabMesg: string = tableMessage;
	showFlag: boolean = true;

	dataList: any = [];
	msgs: Message[] = [];
	code: any;


	eventDefinitionBean: EventDefinitionBean = new EventDefinitionBean();


	flagCopy: string;

	
	orgId: string;
	userId: string;

	showDisabled: boolean = false;//可编辑-修改为已发布进行中时，只能修改

	activeIndex: number = 1;
	value: any;//div
	items: any = [];
	basicElement: string;
	showName: boolean = false;


	//事件类型
	eventTypeOptions: any = [];

	//触发方式
	triggerWayOptions = [{ label: '外部触发', value: '2' },
	{ label: '时间触发', value: '1' }
	];

	//执行周期单位
	executionCycleUnitOptions = [{ label: '天', value: '4' },
	{ label: '周', value: '5' },
	{ label: '月', value: '6' },
	{ label: '年', value: '7' }
	];

	//操作类型 1:新增 2: 修改 
	operflag: string = "1";
	// 是否上一步下一步  0 不是  1 新增的是
	stepflag: string = "1";

	setInValue: any = {
		timeName: '',
	}


	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public eventcenterHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.eventTypeOptions = this.code['eceventTypeList'];//事件类型
	}

	ngOnInit() {

		this.operflag  = this.commfunc.getSessionDataCH('operflag');
		this.stepflag  = this.commfunc.getSessionDataCH('stepflag');

		this.eventDefinitionBean = JSON.parse(this.commfunc.getSessionDataCH('event_eventDefinitionBean'));

		debugger;
		this.activeIndex = 1;
		this.value = "1";
		this.commfunc.setSessionDataCH('index', 1);

		let flagIndex = JSON.parse(this.commfunc.getSessionDataCH('flagIndex'));
		if (flagIndex) {
			this.commfunc.clearSession('flagIndex');
			if (this.commfunc.getSessionDataCH('basicElement')) {

			}
		} else {
			if (this.commfunc.getSessionDataCH('basicElement')) {
				this.commfunc.clearSession('basicElement');
			}
		}

		
		if (this.operflag == '1' && this.stepflag == '0') {
			this.eventDefinitionBean.triggerWay = '2';
			this.eventDefinitionBean.executionCycleUnit = '4';
		}

		

		



		// cx 步骤条样式
		setTimeout(() => {
			let steps = document.getElementById('compId');
			let stepStyle = steps.getElementsByClassName('ui-steps-number');
			for (let i = 0; i < stepStyle.length; i++) {
				stepStyle[i]['style'].backgroundColor = '#757575';
				if (i < this.activeIndex) {
					stepStyle[i]['style'].backgroundColor = '#66CC00';
				}
				if (i == this.activeIndex) {
					stepStyle[i]['style'].backgroundColor = '#ffc107';
				}
			}
		}, 200);
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}


	last() {

		this.commfunc.setSessionDataCH('operflag',this.operflag);
		this.commfunc.setSessionDataCH('stepflag','1');
		this.commfunc.setSessionDataCH('event_eventDefinitionBean', JSON.stringify(this.eventDefinitionBean));
		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/create-eventcenter-activite/ecenter-baseinfo'])
	}


	next() {

		if (!this.eventDefinitionBean.triggerWay) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '触发方式不能为空' });
			return;
		}

		//如果触发方式 为 时间触发
		if (this.eventDefinitionBean.triggerWay == '1') {

			//首次执行时间不能为空
			if (!this.eventDefinitionBean.firstExecutionTime) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '首次执行时间不能为空' });
				return;
			}

			//首次执行时间，可选范围在事件开始时间，事件结束时间之间

			let firstExecutionTime = this.eventDefinitionBean.firstExecutionTime;

			let startTime = this.eventDefinitionBean.eventStartTime;
			let endTime = this.eventDefinitionBean.eventEndTime;

			debugger;
			
			let flagstart = this.commfunc.judgeexpireDate(startTime, firstExecutionTime);

			let flagend = this.commfunc.judgeexpireDate(firstExecutionTime,endTime);

			if (flagstart) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '首次执行时间不能小于开始日期，请重新输入！' });
				return;
			}

			if (flagend) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '首次执行时间不能大于结束日期，请重新输入！' });
				return;
			}




			//执行周期不能为空
			if (!this.eventDefinitionBean.executionCycle) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '执行周期不能为空' });
				return;
			}

			//执行周期单位不能为空
			debugger;
			if (!this.eventDefinitionBean.executionCycleUnit) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '执行周期单位不能为空' });
				return;
			}

			

		}


		this.commfunc.setSessionDataCH('operflag',this.operflag);
		this.commfunc.setSessionDataCH('stepflag',this.stepflag);
		this.commfunc.setSessionDataCH('event_eventDefinitionBean', JSON.stringify(this.eventDefinitionBean));
		this.commfunc.setSessionDataCH('index', 2);


	
		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/create-eventcenter-activite/ecenter-ensure'])
		
		// cx 步骤条样式
		let this_ = this;
		setTimeout(function () {
			let steps = document.getElementById('compId');
			let stepStyle = steps.getElementsByClassName('ui-steps-number');
			for (let i = 0; i < stepStyle.length; i++) {
				stepStyle[i]['style'].backgroundColor = '#757575';
				if (i <= this_.activeIndex - 1) {
					stepStyle[i]['style'].backgroundColor = '#66CC00';
				}
				if (i == (this_.activeIndex)) {
					stepStyle[i]['style'].backgroundColor = '#ffc107';
				}
			}
		}, 200);

	}

	// 触发方式
	triggerWayChange() {

		// //如果响应类型是 通知客户
		// if('2' == this.eventDefinitionBean.triggerWay )
		// {
		// 	this.istzkh = true;

		// }else
		// {
		// 	this.istzkh = false;
		// }

	}


	returnBtn() {
		// setTimeout(() => {
		// 	window.history.go(-1);
		// }, 500)
		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-query'])
	}

	// 时间组件返回值
	getOutValue(event) {
		console.log(event)
		this.eventDefinitionBean.firstExecutionTime = event.time;

		//this.taskBean.marMonitoringTimeTo =  event.time;
	}

}
