import { Component, OnInit } from '@angular/core';
import { Output } from '@angular/core';
import { Input } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { CALENDAR_CN } from '../../credit-ccm/common/http/credit-ccm.http.constant';
import { DatePipe } from '@angular/common';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';

@Component({
    selector: 'ecenter-time',
    templateUrl: './ecenter-time.component.html',
    styleUrls: ['./ecenter-time.component.scss'],
    providers: [DatePipe]
})
/**
 * 时间选择组件
 * 入参
 *  fromDate 回显日期
    deployTimeStart 最小时间选择
    deployTimeEnd  最大时间选择
    timeName 字段名
 */
export class EcenterTimeComponent implements OnInit {

    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    // 传入数据
    fromDate: Date = new Date(); // 回显日期
    deployTimeStart: Date = null; // 最小时间选择
    deployTimeEnd: Date = null; // 最大时间选择
    timeName: string; // 字段名

    ch: any = CALENDAR_CN;

    constructor(
        public datePipe: DatePipe,
        public commfunc: Commfunc,
    ) { }

    ngOnInit() {
        this.timeName = this.inValue.timeName;
        this.timeClick();
    }

    timeClick() {
        let toTime = this.datePipe.transform(this.fromDate, 'yyyy-MM-dd HH:mm:ss')
        console.log(toTime)
        this.outValue.emit({time: toTime}) 
    }

    addTime(col) {
        this.fromDate = this.commfunc.DateAdd(col, 1, this.fromDate, 'add');
        let toTime = this.datePipe.transform(this.fromDate, 'yyyy-MM-dd HH:mm:ss')
        console.log(toTime)
        this.outValue.emit({time: toTime}) 
    }


}
